.portfolio {

  &__container {
    padding: 20px;
    margin: 0 auto;
    max-width: 1200px;
    background: #fff;
  }

  &__title {
    font-size: 36px;
    font-weight: 400;
    margin: 0;
    padding: 20px 0;
  }

  &__info-text {
    padding: 0 20px 0 0;

    &_list {
      padding-left: 0;
    }

    & h2 {
      margin: 0;
    }

  }

  &__info {
    position: relative;
    padding: 20px 0;

    &:after {
      clear: both;
      content: "";
      display: table;
    }

    & img {
      width: 100%;
      padding: 0;
    }

  }

  &__cl {
    display: block;
    float: left;
    width: 50%;
  }

  &__cr {
    position: relative;
    display: block;
    float: left;
    width: 50%;
  }

  &__detail-link {
      display: inline-block;
      padding: 20px 40px;
      margin-bottom: 20px;
      background: #3BC391;
      color: #fff !important;
      text-decoration: none;
      transition: all 0.3s cubic-bezier(.25,.8,.25,1);

      &:hover {
        background: #3BC391;
        box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
      }

      &:active {
        background: #33a97e;
      }
  }

  &__content-list {
    color: #333;

    & h2 {
      line-height: 36px;
    }

    & hr {
      display: block;
      height: 1px;
      border: 0;
      border-top: 1px solid #ccc;
      margin: 1em 0;
      padding: 0;
    }

    & p {
      padding: 0;
      font-size: 16px;
      line-height: 26px;
    }

    & img {
      width: 100%;
      padding: 0;
    }
  }

  &__content {
    color: #333;

    & h2 {
      margin-top: 60px;
      line-height: 36px;

      &:first-child {
        margin: 0;
      }

    }

    & hr {
      display: block;
      height: 1px;
      border: 0;
      border-top: 1px solid #ccc;
      margin: 1em 0;
      padding: 0;
    }

    & p {
      padding: 10px 0;
      font-size: 16px;
      line-height: 26px;
    }

    & img {
      width: 100%;
      padding: 0;
    }

    & a {
      color: #0277BD;
      text-decoration: none;
    }

    & ol {
      line-height: 26px;
      padding: 0 20px 20px 20px;
    }

    & ul {
      line-height: 26px;
      padding: 0 20px 20px 20px;
    }

  }

}


/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {

  .portfolio {

    &__cl {
      width: 50%;
    }

    &__cr {
      width: 50%;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .portfolio {

    &__container {
      padding: 10px;
    }

    &__cl {
      width: 100%;
    }

    &__cr {
      width: 100%;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {

  .portfolio {

    &__cl {
      width: 100%;
    }

    &__cr {
      width: 100%;
    }

  }
}

