.project {

  &__title {
    display: inline-block;
    height: 100px;
    position: absolute;
    padding: 0;
    //background: rgba(68, 255, 81, 0.9);
    top: -60px;

    & h2 {
      display: inline-block;
      color: #fff;
      margin: 0;
      padding-left: 40px;
      font-weight: 300;
      font-size: 30px;
      text-shadow: 1px 1px 1px rgba(0, 0, 0, .5);
    }

  }

  &__all-link {
    width: 100%;
    padding-top: 10px;
    text-align: center;

    & a {
      display: inline-block;
      padding: 20px 40px;
      background: #3BC391;
      color: #fff;
      text-decoration: none;
      transition: all 0.3s cubic-bezier(.25,.8,.25,1);

      &:hover {
        background: #3BC391;
        box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
      }

      &:active {
        background: #33a97e;
      }

    }

  }

  &__container {
    position: relative;
    padding: 0px 9px 0 10px;

    &:after {
      clear: both;
      content: "";
      display: table;
    }

  }

  &__item {
    position: relative;
    display: block;
    float: left;
    width: 33.33%;
    height: auto;
    max-height: 300px;
    overflow: hidden;
    margin-bottom: 0px;

    &_page {
      width: 50%;
    }

    &:hover &-info {
      opacity: 1;
      transform: translateY(-40px) translateZ(0);
    }


    & img {
      background-attachment: inherit;
      width: 100%;
      height: auto;
      transition: all .2s;
    }

    & a {
      display: block;
      margin: 0 0px 0 0;
      overflow: hidden;
      color: #fff;
      text-decoration: none;
    }


    &-info {
      opacity: 0;
      background: rgba(55,64,71,1);
      margin-right: 40px;
      padding: 20px 60px 20px 40px;
      position: absolute;
      bottom: -40px;
      transition: all .2s;
      transform: translateY(-43px) translateZ(0);

      &:after {
        content: '';
        position: absolute;
        top: 25px;
        right: 20px;
        color: #3BC391;
        width: 24px;
        height: 24px;
        background-image: url("../images/ra.png");
      }

      & h3 {
        margin: 0;
        line-height: 30px;
        font-size: 16px;
        font-weight: 400;
      }

    }

  }
}

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {

  .project {

    &__item {
      width: 50%;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .project {

    &__item {
      width: 50%;
    }

    &__title {
      top: -40px;

      & h2 {
        font-size: 22px;
        padding-left: 20px;
      }

    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {
  .project {

    &__item {
      width: 100%;
    }

    &__title {

      & h2 {
        padding-left: 20px;
        font-size: 22px;
      }

    }

  }
}

